<template>
  <div class="vote-detail">
    <!-- main -->
    <detail-main :detail="detail" pageName="'survey'" :isAdmin="true" :statusTxt="statusTxt" />
    <!-- form -->

    <question-list class="question" :class="{'active': !showStatis, 'no-margin-bottom': !showNot}" :showStatis="showStatis" :isAdmin="true" :questions="questions">
      <div class="collaps">
        <a href="javascript:" :class="{'slide-up': showStatis, 'slide-down': !showStatis}" @click="showStatis = !showStatis"></a>
      </div>
    </question-list>

    <!-- statistics -->
    <div class="statis">
      <survey-statistics :showStatis="showStatis" :count="count" :ajax="ajax" @changeShowNot="getShowNot" @remind="remind"/>
    </div>
  </div>
</template>

<script>
import service from '@/service';
import DetailMain from '@/components/VoteSurveyDetail';
import QuestionList from './SurveyQuestionList';
import SurveyStatistics from '@/components/VoteSurveyParticipation';
export default {
  data() {
    return {
      showStatis: true,
      detail: {},
      questions: [],
      statusTxt: ['未开始', '进行中', '已结束', '已参与'],
      // 参与人数统计
      count: {
        hadJoin: 0,
        notJoin: 0,
        canRemind: 1
      },
      showNot: true,
      // 获取参与成员的请求参数
      ajax: {
        hadJoin: {
          method: 'post',
          url: service.survey.hadJoin(),
          opt: {
            id: this.$route.params.id,
            offset: 0,
            limit: 10
          }
        },
        notJoin: {
          method: 'post',
          url: service.survey.notJoin(),
          opt: {
            id: this.$route.params.id,
            offset: 0,
            limit: 10
          }
        }
      }
    };
  },
  created() {
    this._getDetail();
  },
  methods: {
    // 投票详情
    _getDetail() {
      service.survey
        .getDetail({ id: this.$route.params.id })
        .then(res => {
          if (res.data.code === 200) {
            let data = res.data.data;
            this.detail = data.detail;
            this.questions = data.question_info;
            this.count = {
              hadJoin: data.join_mem_count,
              notJoin: data.unjoin_mem_count,
              canRemind: data.detail.canRemind
            };
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    // 提醒参与
    remind() {
      service.survey.remind({ id: this.$route.params.id })
        .then(res => {
          if (res.data.code === 200) {
             this.count.canRemind = 0;
            alert('已提醒');
          }
        }).catch(err => {
          console.error(err);
        });
    },
    // 获取是否显示未参与
    getShowNot(flag) {
      this.showNot = flag;
    }
  },
  components: {
    DetailMain,
    QuestionList,
    SurveyStatistics
  }
};
</script>

<style lang="stylus" scoped>
@import "../../common/stylus/base"

.vote-detail
  position absolute
  top 0
  bottom 0
  right 0
  left 0
  display flex
  flex-direction column
  background-color #f4f4f4
  font-size 0
  .question
    margin-bottom 0.2rem
    &.active
      margin-bottom 1.4rem
      &.no-margin-bottom
        margin-bottom 0
    .collaps
      padding 0.3rem 0 0.5rem
      background-color #fff
      text-align center
      font-size 0.4rem
      color #777
      .slide-up::after
        content "查看详情"
        padding-right 0.4rem
        background url("../../assets/arrow-down.png") no-repeat right 0.14rem
        background-size 0.3rem 0.192rem
      .slide-down::after
        content "收起"
        padding-right 0.4rem
        background url("../../assets/arrow-up.png") no-repeat right 0.1rem
        background-size 0.3rem 0.192rem
  .statis
    flex-grow 1
    flex-shrink 0
    position relative
</style>




