<template>
  <survey-list :ajax="ajax" :statusTxt="statusTxt" :pageName="'survey'" />
</template>

<script>
import service from '@/service';
import SurveyList from '@/components/VoteSurveyList';
export default {
  data() {
    return {
      ajax: {
        method: 'post',
        url: service.survey.getList(),
        opt: { limit: 10, offset: 0 }
      },
      statusTxt: ['未开始', '进行中', '已结束', '已参与']
    };
  },
  components: {
    SurveyList
  }
};
</script>
<style scoped lang="stylus">
</style>




