<template>
  <div class="take-part-list">
    <vote-statistics :pageName="'vote'" :ajax="ajax" :count="count" @remind="remind"/>
  </div>
</template>

<script>
import service from '@/service';
import VoteStatistics from '@/components/VoteSurveyParticipation';
export default {
  props: {
    pageName: String
  },
  data() {
    return {
      // 参与成员人数
      count: {
        hadJoin: Number(this.$route.query.had),
        notJoin: Number(this.$route.query.not),
        canRemind: Number(this.$route.query.canremind)
      },
      // 获取参与成员的请求参数
      ajax: {
        hadJoin: {
          method: 'post',
          url: service.vote.hadJoin(),
          opt: {
            id: this.$route.params.id,
            offset: 0,
            limit: 10
          }
        },
        notJoin: {
          method: 'post',
          url: service.vote.notJoin(),
          opt: {
            id: this.$route.params.id,
            offset: 0,
            limit: 10
          }
        }
      }
    };
  },
  methods: {
    // 提醒参与
    remind() {
      service.vote.remind({ id: this.$route.params.id })
        .then(res => {
          if (res.data.code === 200) {
            this.count.canRemind = 0;
            alert('已提醒');
          }
        })
        .catch(err => {
          console.error(err);
        });
    }
  },
  components: {
    VoteStatistics
  }
};
</script>

<style lang="stylus" scoped>
.take-part-list
  position absolute
  top 0
  left 0
  bottom 0
  right 0
  background-color #fff
</style>





