<template>
  <div class="vote-detail">
    <template v-if="detail.status != 0 && detail.status != 2">
      <detail-main :detail="detail" :pageName="'vote'" :isAdmin="false" :statusTxt="statusTxt" />
      <div class="form">
        <div class="type" :class="{'radio': detail.type == 1, 'multi': detail.type == 2}">{{checkTitle}}</div>
        <form class="question-form" @submit.prevent="submitForm" ref="form">
          <ul>
            <li v-for="(item, index) in options" :key="index">
              <input :type="detail.type==1?'radio':'checkbox'" name="answer" :id="`check_${index}`" :checked="item.checked" :value="index+1" :disabled="detail.status==4&&detail.msg">
              <label :for="`check_${index}`">
                <span class="icon"></span>
                <span>{{thumbs[index]}}、</span>
                <span>{{item.title}}</span>
              </label>
            </li>
          </ul>
          <div class="btn" v-if="!detail.msg && detail.status != 3">
            <button type="submit">提交</button>
          </div>
        </form>
      </div>
    </template>
    <div class="tips" v-else>
      <span>{{tips}}</span>
    </div>
  </div>
</template>

<script>
import service from '@/service';
import DetailMain from '@/components/VoteSurveyDetail';
export default {
  data() {
    return {
      // 状态码
      statusTxt: ['未开始', '进行中', '已结束', '暂停', '已投票'],
      // 投票详情
      detail: {
        status: '0'
      },
      // 序号
      thumbs: ['A', 'B', 'C', 'D', 'E', 'F', 'G', 'H', 'I', 'J', 'K', 'L', 'M'],
      // 选项
      options: [],
      // 标题
      checkTitle: '',
      // 选项限制
      checkLimit: {},
      // 提示
      tips: ''
    };
  },
  created() {
    service.vote
      .getDetail({ id: this.$route.params.id })
      .then(res => {
        if (res.data.code === 200) {
          let data = res.data.data;
          this.detail = data.detail;
          this.options = data.question_info.option;
          if (String(data.detail.status) === '2') {
            this.tips = '投票已结束！';
          } else if (String(data.detail.status) === '0') {
            this.tips = '投票未开始，敬请期待';
          }
          if (String(data.detail.type) === '2') {
            let checkLimit = JSON.parse(data.question_info.extra);
            this.checkLimit = checkLimit;
            this.checkTitle = `多选【请选择${checkLimit.least}到${checkLimit.most}项】`;
          } else {
            this.checkTitle = '单选';
          }
        }
      })
      .catch(err => {
        console.log(err);
      });
  },
  methods: {
    // 提交表单
    submitForm() {
      let formObj = [];
      let checks = this.$refs.form.querySelectorAll('input');
      checks.forEach(function(ele, index) {
        if (ele.checked) {
          formObj.push(ele.value);
        }
      });
      if (!formObj.length) {
        alert('请选择投票项');
        return;
      }
      if ('least' in this.checkLimit) {
        if (formObj.length > this.checkLimit.most) {
          alert(`请选择不要多于${this.checkLimit.most}项`);
          return;
        } else if (formObj < this.checkLimit.least) {
          alert(`请选择不要少于${this.checkLimit.least}项`);
          return;
        }
      }
      formObj = +formObj ? +formObj : String(formObj);
      service.vote
        .submit({ id: this.$route.params.id, answer: formObj })
        .then(res => {
          if (res.data.code === 200) {
            this.detail.status = '4';
            alert(res.data.data.finish);
            window.history.go(-1);
          }
        })
        .catch(err => {
          console.error(err);
        });
    }
  },
  components: {
    DetailMain
  }
};
</script>

<style lang="stylus" scoped>
@import "../../common/stylus/base"

.vote-detail
  position absolute
  top 0
  bottom 0
  left 0
  right 0
  padding-bottom 0.8rem
  background-color #f4f4f4
  font-size 0
  .form
    position relative
    padding-top 0.2rem
    background-color #fff
    margin 0.2rem 0 2rem
    .type
      height 0.5rem
      padding-left 1rem
      margin-bottom 0.3rem
      line-height 0.5rem
      font-size 0.4rem
    .type.multi
      background url("../../assets/multi-check-bg.png") no-repeat 0.2rem 0
      background-size contain
    .type.radio
      background url("../../assets/radio-check-bg.png") no-repeat 0.2rem 0
      background-size contain
    .btn
      position absolute
      width 100%
      bottom -1.7rem
      text-align center
      button
        width 4rem
        height 0.8rem
        border none
        border-radius 0.05rem
        background-color $color-main
        color #fff
        font-size 0.36rem
  .tips
    position absolute
    top 0
    right 0
    left 0
    bottom 0
    display flex
    align-items center
    justify-content center
    span
      font-size 0.6rem
      color $color-main
</style>



