<template>
  <div class="survey-detail">
    <template v-if="detail.status == 1 || detail.status == 3">
      <detail-main :detail="detail" :pageName="'survey'" :isAdmin="false" :statusTxt="statusTxt" />
      <question-list :questions="questions" :isAdmin="false" @submit="submitForm" :hadJoin="detail.status == 3 ? true : false" />
    </template>
    <div class="tip" v-else>
      <span>{{tips}}</span>
    </div>
  </div>
</template>

<script>
import service from '@/service';
import { formData } from '@/common/js/dom';
import DetailMain from '@/components/VoteSurveyDetail';
import QuestionList from './SurveyQuestionList';
export default {
  data() {
    return {
      statusTxt: ['未开始', '进行中', '已结束', '已参与'],
      detail: {
        status: '0'
      },
      questions: [],
      tips: ''
    };
  },
  created() {
    this._getDetail();
  },
  methods: {
    // 获取详情
    _getDetail() {
      service.survey
        .getDetail({ id: this.$route.params.id })
        .then(res => {
          if (res.data.code === 200) {
            let response = res.data.data;
            if (response.detail) {
              if (Number(response.detail.status) === 0) {
                this.tips = '调研未开始，敬请期待！';
              } else if (Number(response.detail.status) === 2) {
                this.tips = '调研已结束！';
              } else {
                this.detail = response.detail;
                this.questions = response.question_info;
              }
            }
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    // 表单提交
    submitForm(form) {
      let tempData = formData(form);
      let errorKeys = Object.keys(tempData.errors);
      if (errorKeys.length) {
        console.log(tempData.errors);
        window.scrollTo(0, tempData.errors[errorKeys[0]].offsetTop);
        return;
      } else {
        this.answer = tempData.data;
        console.log(this.answer);
      }
      service.survey
        .submit({ id: this.$route.params.id, answer: this.answer })
        .then(res => {
          if (res.data.code === 200) {
            alert(res.data.data.finish);
            this.$set(this.detail, status, '3');
            window.history.go(-1);
          }
        })
        .catch(err => {
          console.error(err);
        });
    }
  },
  components: {
    DetailMain,
    QuestionList
  }
};
</script>

<style lang="stylus" scoped>
@import "../../common/stylus/base"

.survey-detail
  .tip
    position absolute
    top 0
    left 0
    bottom 0
    right 0
    display flex
    justify-content center
    align-items center
    background-color #f4f4f4
    span
      font-size 0.6rem
      color $color-main
</style>



