<template>
  <div class="wrapper">
    <div class="title" v-show="showStatis">
      <a href="javascript:" class="abtn" :class="{'active': showNot}" @click="toggleShowNot(true)">未参与 ({{count.notJoin}})</a>
      <a href="javascript:" class="abtn" :class="{'active': !showNot}" @click="toggleShowNot(false)">已参与 ({{count.hadJoin}})</a>
    </div>
    <div class="lists" v-show="showStatis">
      <scroll-view class="not scroll-wrapper" v-show="showNot" :data="notJoinList" :pullup="pullup" @scrollToEnd="getMoreNotJoin">
        <div class="scroll-content" v-show="count.notJoin">
          <ul class="list">
            <li class="item" v-for="(item, index) in notJoinList" :key="index">
              <img class="avatar" :src="item.avatar || defaultAvatar" alt="">
              <div class="des">
                <div class="top">
                  <span class="name">{{item.name}}</span>
                </div>
              </div>
            </li>
          </ul>
          <loading-tip :tip="notJoinLoadingTip" />
          <p class="tips" v-if="!count.notJoin">暂无成员</p>
        </div>
      </scroll-view>
      <scroll-view class="had scroll-wrapper" v-show="!showNot" :data="hadJoinList" :pullup="pullup" @scrollToEnd="getMoreHadJoin">
        <div class="scroll-content" v-show="count.hadJoin">
          <ul class="list">
            <li class="item" v-for="(item, index) in hadJoinList" :key="index">
              <img class="avatar" :src="item.avatar || defaultAvatar" alt="">
              <div class="des">
                <div class="top">
                  <span class="name">{{item.name}}</span>
                  <span class="time">{{item.time}}</span>
                </div>
                <div class="bottom" v-if="pageName === 'vote'">
                  已投“<ins class="highlight">{{item.option}}</ins>”
                </div>
              </div>
            </li>
          </ul>
          <loading-tip :tip="hadJoinLoadingTip" />
        </div>
        <p class="tips" v-if="!count.hadJoin">暂无成员</p>
      </scroll-view>
    </div>
    <div class="btn" v-show="showNot">
      <button @click="remind" :disabled="!count.canRemind">{{count.canRemind ? '一键提醒' : '已提醒'}}</button>
    </div>
  </div>
</template>

<script>
import axois from 'axios';
import ScrollView from '@/components/scroll/ScrollView';
import LoadingTip from '@/components/tips/LoadingTip';
export default {
  name: 'VoteSurveyParticipation',
  props: {
    // 标记是调研还是统计
    pageName: String,
    // 是否显示统计
    showStatis: {
      type: Boolean,
      default: true
    },
    // 参与人数
    count: {
      hadJoin: Number,
      notJoin: Number,
      canRemind: Number
    },
    // 获取参与成员
    ajax: {
      hadJoin: {
        method: {
          type: String,
          default: 'post'
        },
        url: String,
        opt: {
          id: [String, Number],
          offset: Number,
          limit: Number
        }
      },
      notJoin: {
        method: {
          type: String,
          default: 'post'
        },
        url: String,
        opt: {
          id: [String, Number],
          offset: Number,
          limit: Number
        }
      }
    }
  },
  data() {
    return {
      hadJoinList: [], // 已参与成员
      notJoinList: [], // 未参与成员
      showNot: true, // 是否显示未参与
      pullup: true, // scroll
      notJoinLoadingTip: '', // 加载成员提示
      hadJoinLoadingTip: '', // 加载成员提示
      defaultAvatar: '../../static/image/default-avatar.png' // 默认头像
    };
  },
  created() {
    this._getNotJoin();
    this._getHadJoin();
  },
  methods: {
    // 获取已参与成员
    _getHadJoin() {
      let params = this.ajax.hadJoin;
      let opt = params.opt;
      if (opt.over) {
        return;
      }
      this.hadJoinLoadingTip = '正在加载中';
      axois[params.method](params.url, opt)
        .then(res => {
          let data = res.data;
          let list = data.data.mem_info;
          if (data.code === 200) {
            this.hadJoinList.push(...list);
          }
          if (list.length < opt.limit) {
            this.hadJoinLoadingTip = '全部加载完毕';
            opt.over = true;
          }
        })
        .catch(err => {
          console.error(err);
        });
    },
    // 获取未参与成员
    _getNotJoin() {
      let params = this.ajax.notJoin;
      let opt = params.opt;
      if (opt.over) {
        return;
      }
      this.notJoinLoadingTip = '正在加载中';
      axois[params.method](params.url, opt)
        .then(res => {
          let data = res.data;
          let list = data.data.mem_info;
          if (data.code === 200) {
            this.notJoinList.push(...list);
          }
          if (list.length < opt.limit) {
            this.notJoinLoadingTip = '全部加载完毕';
            opt.over = true;
          }
        })
        .catch(err => {
          console.error(err);
        });
    },
    // 加载更多已参与成员
    getMoreHadJoin() {
      this.ajax.hadJoin.opt.offset += 10;
      this._getHadJoin();
    },
    // 加载更多未参与成员
    getMoreNotJoin() {
      this.ajax.notJoin.opt.offset += 10;
      this._getNotJoin();
    },
    // 提醒参与
    remind() {
      this.$emit('remind');
    },
    // 切换参与未参与列表
    toggleShowNot(flag) {
      this.showNot = flag;
      this.$emit('changeShowNot', flag);
    }
  },
  components: {
    ScrollView,
    LoadingTip
  }
};
</script>
<style lang="stylus" scoped>
@import "../common/stylus/base"

.wrapper
  display flex
  flex-direction column
  height 100%
  background-color #f4f4f4
  font-size 0
  .title
    flex-shrink 0
    display flex
    align-items center
    position relative
    z-index 10
    border-bottom 0.01rem solid #888
    background-color #fff
    font-size 0.4rem
    .abtn
      width 50%
      height 0.85rem
      line-height 0.8rem
      border-bottom 0.05rem solid transparent
      text-align center
      color #212121
      &.active
        color $color-main
        border-color $color-main
  .lists
    flex-grow 1
    .scroll-wrapper
      position absolute
      top 0.85rem
      width 100%
      overflow hidden
      .tips
        line-height 2rem
        text-align center
        font-size 0.36rem
        color #777
      &.not
        bottom 1.4rem
      &.had
        bottom 0
      .list
        padding 0 0.25rem
        background-color #fff
        .item
          display flex
          align-items center
          padding 0.1rem 0.05rem
          border-bottom 0.01rem solid #eee
          .avatar
            border-radius 50%
            background-color #aaa
            width 1rem
            height 1rem
            margin-right 0.3rem
          .des
            flex-grow 1
            display flex
            flex-direction column
            .top
              display flex
              flex-direction row
              justify-content space-between
              align-items center
              .name
                font-size 0.38rem
              .time
                flex-grow 1
                align-self flex-start
                margin-top 0.1rem
                text-align right
                font-size 0.34rem
                color #777
            .bottom
              font-size 0.34rem
              text-align justify
              .highlight
                text-decoration none
                color $color-main

  .btn
    position fixed
    bottom 0
    width 100%
    padding 0.3rem 0
    text-align center
    background-color #f4f4f4
    button
      width 4rem
      height 0.8rem
      border 0.01rem solid $color-main
      border-radius 0.08rem
      background-color transparent
      color $color-main
      font-size 0.42rem
</style>




