<template>
  <scroll-view class="survey-list-wrapper" :data="items" :pullup="pullup" @scrollToEnd="getMoreItems">
    <div class="scroll-content">
      <ul class="survey-list" :class="[pageName]">
        <transition-group name="fade">
          <li class="item" v-for="item in items" :key="item.id">
            <router-link :to="linkToDetail(item)">
              <div class="cover">
                <img :src="item.cover" alt="封面">
              </div>
              <div class="brief">
                <span class="title">{{item.title}}</span>
                <span class="author">{{item.author}}</span>
              </div>
              <div class="stamp">
                <span class="time">{{item.time}}</span>
                <span class="status" :status="item.status">{{parseStatus(item.status)}}</span>
              </div>
            </router-link>
          </li>
        </transition-group>
      </ul>
      <loading-tip :tip="loadingTip" />
    </div>
  </scroll-view>
</template>

<script>
import axios from 'axios';
import ScrollView from '@/components/scroll/ScrollView';
import LoadingTip from '@/components/tips/LoadingTip';
export default {
  name: 'VoteSurveyList',
  props: {
    // 页面名称
    pageName: '',
    // 状态
    statusTxt: Array,
    // 请求参数
    ajax: {
      method: {
        type: String,
        default: 'post'
      },
      url: String,
      opt: {
        offset: Number,
        limit: Number
      }
    }
  },
  data() {
    return {
      isAdmin: 0,
      items: [], // 调研列表
      pullup: true, // 上拉刷新
      loadingTip: '' // 加载提示
    };
  },
  created() {
    this.getItems();
  },
  methods: {
    // 获取调研列表
    getItems() {
      let opt = this.ajax.opt;
      if (opt.over) {
        return;
      }
      this.loadingTip = '正在加载中';
      axios[this.ajax.method](this.ajax.url, opt)
        .then(res => {
          if (res.data.code === 200) {
            let list = res.data.data.list;
            this.isAdmin = Number(res.data.data.isAdmin);
            this.items.push(...list);
            if (!list.length && !opt.offset) {
              this.loadingTip = '暂无数据';
              opt.over = true;
            } else if (list.length < opt.limit) {
              this.loadingTip = '全部加载完毕';
              opt.over = true;
            }
          } else {
            console.error(res.data.msg);
          }
        })
        .catch(err => {
          this.loadingTip = '加载失败';
          console.log(err);
        });
    },
    // 上拉获取更多调研
    getMoreItems() {
      this.ajax.opt.offset += this.ajax.opt.limit;
      this.getItems();
    },
    // 状态
    parseStatus(status) {
      return this.statusTxt[Number(status)];
    },
    // 详情页链接
    linkToDetail(item) {
      let base = `/${this.pageName}/${item.id}/detail/aid/${this.$route.params.aid}`;
      return this.isAdmin ? `${base}/admin` : base;
    }
  },
  components: {
    ScrollView,
    LoadingTip
  }
};
</script>
<style scoped lang="stylus">
@import "../common/stylus/base"

.fade-enter-active, .fade-leave-active
  opacity 1
  transform translateY(0)
  transition all 1.5s
.fade-enter, .fade-leave-to
  opacity 0
  transform translateY(1rem)
.survey-list-wrapper
  position absolute
  width 100%
  height 100%
  background-color #efefef
  font-size 0
  .survey-list
    .item
      &>a
        background-color #fff
        display flex
        padding 0.1rem
        margin-bottom 0.2rem
        .cover
          margin-right 0.1rem
          img
            width 1rem
            height 1rem
            background-color #ccc
        .brief
          flex-grow 0
          display flex
          flex-direction column
          justify-content space-between
          flex-grow 1
          .title
            width 3rem
            overflow-x hidden
            white-space nowrap
            text-overflow ellipsis
            font-size 0.34rem
          .author
            font-size 0.32rem
            color #999
        .stamp
          flex-shrink 0
          display flex
          flex-direction column
          justify-content space-between
          .time
            font-size 0.32rem
            color #999
.survey
  .status
    font-size 0.32rem
    text-align right
    &[status="0"]
      color #fd7c14
    &[status="1"]
      color #2dc56d
    &[status="2"]
      color #d62525
    &[status="3"]
      color #b42632
.vote
  .status
    font-size 0.32rem
    text-align right
    &[status="0"]
      color #fd7c14
    &[status="1"]
      color #2dc56d
    &[status="2"]
      color #d62525
    &[status="3"]
      color #f79f06
    &[status="4"]
      color #b42632
</style>




