export function formData(form) {
  if (!form) {
    console.error('argument "form" expect dom');
    return;
  }
  let data = {}; // 填写的表单
  let errors = {}; // 校验失败的数据
  let inputs = form.querySelectorAll('input, textarea');
  inputs.forEach(function(ele) {
    if (!ele.name) {
      return;
    }
    if (ele.tagName === 'INPUT') {
      if (ele.type === 'radio' || ele.type === 'checkbox') {
        saveCheck(ele);
      } else {
        saveTextVal(ele);
      }
    } else if (ele.tagName === 'TEXTAREA') {
      saveTextVal(ele);
    } else if (ele.tagName === 'SELECT') {
      //
    }
  }, this);

  function saveTextVal(ele) {
    if (ele.value.trim()) {
      data[ele.name] = ele.value;
    }
    if (ele.required) {
      if (data[ele.name]) {
        delete errors[ele.name];
      } else {
        errors[ele.name] = {
          error: '请把表单填写完整',
          offsetTop: getParentNode(ele, 'item').offsetTop
        };
      }
    }
  }
  function saveCheck(ele) {
    if (ele.checked) {
      if (ele.type === 'radio') {
        data[ele.name] = ele.value;
      } else {
        data[ele.name] = data[ele.name]
          ? data[ele.name].concat(ele.value)
          : [].concat(ele.value);
      }
    }
    if (ele.required) {
      if (data[ele.name]) {
        if (ele.type === 'checkbox') {
          let least = ele.getAttribute('least');
          let most = ele.getAttribute('most');
          let checkedLen = data[ele.name].length;
          if (least) {
            if (checkedLen < least || checkedLen > most) {
              errors[ele.name] = {
                error: `请选择${least}到${most}项`,
                offsetTop: getParentNode(ele, 'item').offsetTop
              };
            } else {
              delete errors[ele.name];
            }
          } else {
            delete errors[ele.name];
          }
        } else {
          delete errors[ele.name];
        }
      } else {
        errors[ele.name] = {
          error: '请把表单填写完整',
          offsetTop: getParentNode(ele, 'item').offsetTop
        };
      }
    }
  }
  function getParentNode(srcNode, className) {
    let reg = new RegExp(className, 'g');
    let pNode = null;
    while (!reg.test(srcNode.parentNode.className)) {
      srcNode = srcNode.parentNode;
    }
    pNode = srcNode.parentNode;
    return pNode;
  }
  return { errors, data };
}



