<template>
  <form class="question-form" @submit.prevent="submitForm" ref="form" novalidate>
    <ul class="question-list" v-show="!showStatis">
      <li class="item" v-for="(item, indexQ) in questions" :key="item.id">
        <p class="title" :class="{'required': +item.required}">{{`${indexQ + 1}、${item.title}`}}
          <span class="tag">{{parseTag(item)}}</span>
        </p>
        <!-- 选择题 -->
        <ul class="options" v-if="item.type==1 || item.type==2">
          <li class="option" v-for="(opt, indexO) in item.option" :key="indexO">
            <input :type="parseType(item)" :name="item.id" :id="parseId(indexQ, indexO, item)" :value="indexO+1" :checked="opt.checked" :disabled="hadJoin" :required="!!+item.required" :least="item.least" :most="item.most">
            <label :for="parseId(indexQ, indexO, item)">
              <span class="icon"></span>
              <span>{{thumbs[indexO]}}、</span>{{opt.title}}</label>
          </li>
        </ul>
        <!-- 简答题 -->
        <div class="txt" v-if="item.type==3">
          <textarea :name="item.id" :value="item.value" :readonly="hadJoin" :required="!!+item.required"></textarea>
        </div>
        <div class="txt" v-else-if="item.type==4">
          <input type="text" :name="item.id" :value="item.value" :readonly="hadJoin" :required="!!+item.required" />
        </div>
      </li>
    </ul>
    <slot></slot>
    <div class="btn" v-if="!isAdmin && !hadJoin">
      <button type="submit">提交</button>
    </div>
  </form>
</template>

<script>
export default {
  name: 'SurveyDetailAdmin',
  props: {
    // 是否参与
    hadJoin: {
      default: false,
      type: Boolean
    },
    // 问题列表
    questions: Array,
    // 管理员身份
    isAdmin: Boolean,
    // 显示统计信息
    showStatis: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      thumbs: ['A', 'B', 'C', 'D', 'E', 'F', 'G', 'H', 'I', 'J', 'K', 'L']
    };
  },
  methods: {
    // 题目序号
    parseTag(item) {
      let type = String(item.type);
      if (type === '1') {
        return `【单选】`;
      } else if (type === '2') {
        let limit = JSON.parse(item.extra);
        return `【多选】【请选择${limit.least}到${limit.most}项】`;
      } else {
        return `【简答】`;
      }
    },
    // 选项id
    parseId(index1, index2, item) {
      let prefix = '';
      switch (Number(item.type)) {
        case 1:
          prefix = 'radio';
          break;
        case 2:
          prefix = 'checkbox';
          break;
        default:
          prefix = 'short';
      }
      return prefix + index1 + index2;
    },
    // 选项类别
    parseType(item) {
      let type = Number(item.type);
      if (type === 1) {
        return 'radio';
      } else if (type === 2) {
        return 'checkbox';
      }
    },
    // 表单提交
    submitForm() {
      let form = this.$refs.form;
      this.$emit('submit', form);
    }
  }
};
</script>

<style lang="stylus" scoped>
@import "../../common/stylus/base"

.question-form
  font-size 0
  .question-list
    .item
      margin-top 0.2rem
      padding 0.2rem 0
      box-shadow 0 2px 8px 1px #ddd
      background-color #fff
      font-size 0.38rem
      .title
        padding-left 0.2rem
        font-size 0.36rem
        &.required::before
          content "*"
          color $color-main
        .tag
          color $color-main
      .txt
        display flex
        padding 0.2rem 0.5rem
        textarea
          flex-grow 1
          height 2.3rem
          padding 0.05rem
          border 0.01rem solid #ccc
          resize none
          text-align justify
        input
          flex-grow 1
          height 0.6rem
          padding 0 0.05rem
          border 0.01rem solid #ccc
  .btn
    margin 0.5rem 0 0.8rem
    text-align center
    button
      width 4rem
      height 0.8rem
      border none
      border-radius 0.05rem
      background-color $color-main
      color #fff
      font-size 0.36rem
</style>




