<template>
  <vote-list :ajax="ajax" :statusTxt="statusTxt" :pageName="'vote'" />
</template>

<script>
import service from '@/service';
import VoteList from '@/components/VoteSurveyList';
export default {
  data() {
    return {
      ajax: {
        method: 'post',
        url: service.vote.getList(),
        opt: { limit: 10, offset: 0 }
      },
      statusTxt: ['未开始', '进行中', '已结束', '暂停', '已投票']
    };
  },
  components: {
    VoteList
  }
};
</script>
<style scoped lang="stylus">
</style>



